const host = window.location.host;
console.log(host);
let baseurl = "http://localhost:5000";
if (window.location.host !== "localhost:3000") {
  baseurl = "https://hellofoodapi.herokuapp.com";
}

export const Urls = {
  baseurl,
  hfsREST: "/hfs",
  getOrders: "/hfs/orders",
  postOrders: "/hfs/orders",
};

export const getReqJson = (url, config = {}) => {
  return new Promise((resolve, reject) => {
    fetch(url, config)
      .then((res) => {
        if (res.status === 200) {
          return res.json();
        }
        res.json().then((error) => {
          console.log(error);
          return reject(error);
        });
      })
      .then((jsonRes) => {
        setTimeout(() => {
          resolve(jsonRes);
        }, 1000);
      })
      .catch((err) => {
        console.log(err);
        reject(err);
      });
  });
};

export const postReqJson = (url, config = {}) => {
  return new Promise((resolve, reject) => {
    fetch(url, config)
      .then((res) => {
        console.log(res);
        if (res.status === 200) {
          return res.json();
        }

        return reject(res.json());
      })
      .then((jsonRes) => {
        setTimeout(() => {
          resolve(jsonRes);
        }, 1000);
      })
      .catch((err) => {
        console.log(err);
        reject(err);
      });
  });
};
