import { Urls, getReqJson } from "./config";


const Config = (method, body) => {
  return {
    method,
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(body),
  };
};



const postOrder = async (data) => {
  const url = Urls.baseurl + Urls.postOrders;
  const orderList = await getReqJson(url, Config("POST", data));
  return orderList;
};

export {  postOrder };
