import { MinusOutlined, PlusOutlined } from "@ant-design/icons";
import { Button, Card, Rate } from "antd";
import { prodList } from "../data/product";

const { Meta } = Card;

function Products(props) {
  const { cart, setCart } = props.cart;
  const removeItems = (product) => {
    if (cart.items[product.id] && cart.items[product.id] > 0) {
      cart.items[product.id] -= 1;
      cart.totalitems -= 1;
      cart.bill -= product.price;
      setCart({ ...cart });
    }
  };
  const addItems = (product) => {
    if (cart.items[product.id]) {
      cart.items[product.id] += 1;
    } else {
      cart.items[product.id] = 1;
    }
    cart.totalitems += 1;
    cart.bill += product.price;
    setCart({ ...cart });
  };

  return (
    <>
      {prodList.map((product) => (
        <Card
          cover={<img className="product-img" src={product.pic} />}
          actions={[
            <Button onClick={() => removeItems(product)}>
              <MinusOutlined />
            </Button>,
            <h4>{cart.items[product.id] || 0}</h4>,
            <Button onClick={() => addItems(product)}>
              <PlusOutlined key="ellipsis" />
            </Button>,
          ]}
          style={{ width: "250px", margin: "50px" }}
        >
          <Meta title={`${product.name}`} description={product.description} />

          <Rate allowHalf defaultValue={product.rating} />
        </Card>
      ))}
    </>
  );
}

export default Products;