import { Avatar, Badge, Row, Col, Modal, Form, Input, Select, Divider } from "antd";
import { useState } from "react";
import "./App.css";
import { ShoppingCartOutlined } from "@ant-design/icons";
import { prodList } from "../data/product";
import { postOrder } from "../apis/apiHandler";

const { Option } = Select;

function Header(props) {
  const { cart } = props;
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [formData, setFormData] = useState({});

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
    formData.cart = Object.assign({}, cart);
    const itemsTmp = []
    Object.keys(formData.cart.items).forEach(id => {
      const foodItem = prodList.find((food) => food.id == id);
      itemsTmp.push({
        ...foodItem,
        count: formData.cart.items[id]
      });
    });
    formData.cart.items = itemsTmp;
    console.log(formData)
    postOrder(formData)
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);

      });
  };

  const handleInput = (fieldName, fieldValue) => {
    setFormData({ ...formData, [fieldName]: fieldValue });
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      <Row
        style={{
          position: "fixed",
          zIndex: 1,
          width: "100%",
          backgroundColor: "#389e0d",
        }}
      >
        <Col>
          <div>
            <img src="items/logo.png" style={{ maxWidth: "45px", maxHeight: "45px", margin: "5px" }} />
          </div>
        </Col>
        <Col style={{ marginLeft: "2px" }}>
          <div className="brandname">hello food Service</div>
        </Col>

        <Col style={{ marginLeft: "13px", paddingBlock: "13px" }}>
          <Badge count={cart.totalitems > 0 ? cart.totalitems : 0} onClick={showModal}>
            <ShoppingCartOutlined style={{ fontSize: "30px", color: "white" }} />
          </Badge>
        </Col>
      </Row>

      <Modal
        title="Order Summary"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        width="70%"
        okText="Submit"
        okButtonProps={{
          type: "primary",
          htmlType: "submit",
        }}
        cancelButtonProps={{}}
      >
        <Form layout="vertical" hideRequiredMark>
          <Avatar shape="square" size={64} src={"items/logo.png"} /> <b style={{ fontSize: "15px" }}>hello food Service</b> <Divider />
          <b>Product Details: </b>
          {Object.keys(cart.items).map((id) => {
            const foodItem = prodList.find((food) => food.id == id);
            return (
              <Row style={{ marginLeft: "110px" }}>
                <Col>{foodItem.name}</Col>
                <Col>{`${cart.items[id]} X ${foodItem.price} = `}</Col>
                <Col>{Number(cart.items[id]) * Number(foodItem.price)}</Col>
              </Row>
            );
          })}
          <br />
          <Row>
            <b>Total Items : </b>
            <Col style={{ marginLeft: "30px" }}>{cart.totalitems}</Col>
          </Row>
          <Row>
            <b>Total Amount: </b>
            <Col style={{ marginLeft: "15px" }}>{cart.bill}</Col>
          </Row>
          <Divider />
          <h3>Address </h3>
          <Divider />
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                name="name"
                label="Name"
                rules={[
                  {
                    required: true,
                    message: "Please enter user name",
                  },
                ]}
              >
                <Input placeholder="Please enter user name" value={formData.name} onChange={(event) => handleInput("name", event.target.value)} />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                Mobile
                no="mobile"
                label="Mobile"
                rules={[
                  {
                    required: true,
                    message: "Please enter Mobile No ",
                  },
                ]}
              >
                <Input
                  style={{
                    width: "100%",
                  }}
                  placeholder="Please enter mobile no"
                  value={formData.mobile}
                  onChange={(event) => handleInput("mobile", event.target.value)}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={12}>
              <Form.Item
                label="Address"
                Address="address"
                rules={[
                  {
                    required: true,
                    message: "Please enter address",
                  },
                ]}
              >
                <Input
                  style={{
                    width: "100%",
                  }}
                  placeholder="Please enter address"
                  value={formData.address}
                  onChange={(event) => handleInput("address", event.target.value)}
                />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name="email" label="Email">
                <Input
                  style={{
                    width: "100%",
                  }}
                  placeholder="Please enter email"
                  value={formData.email}
                  onChange={(event) => handleInput("email", event.target.value)}
                />
              </Form.Item>
            </Col>
          </Row>
          <Divider />
        </Form>
      </Modal>
    </>
  );
}

export default Header;
