import { Table, Row, Col } from "antd";
import React from "react";

import { Input } from "antd";
import FooterSt from "./footer";

const { Search } = Input;

const columns = [
  {
    title: "Name",
    dataIndex: "name",
    width: 100,
  },
  {
    title: "Mobile No",
    dataIndex: "mobile",
    width: 110,
  },
  {
    title: "Address",
    dataIndex: "address",
  },
  {
    title: "Order",
    dataIndex: "order",
  },
];
const data = [];

for (let i = 0; i < 100; i++) {
  data.push({
    key: i,
    name: `Edward King ${i}`,
    age: 32,
    mobile: `905845958${i}`,
    address: `London, Park Lane no. ${i}`,
  });
}
function Admin() {
  return (
    <>
      <h1><b>Admin page</b></h1>
      <Search placeholder="input search text" enterButton />

      <Table
        columns={columns}
        dataSource={data}
        pagination={{
          pageSize: 50,
        }}
        scroll={{
          y: 590,
        }}
      />
      <FooterSt></FooterSt>
    </>
  );
}

export default Admin;
